  src: url("../fonts/montserrat/Montserrat-Bold.woff2") format("woff2"), url("../fonts/montserrat/Montserrat-Bold.woff") format("woff");
  font-weight: bold;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: 'Montserrat-Regular';
  src: url("../fonts/montserrat/Montserrat-Regular.woff2") format("woff2"), url("../fonts/montserrat/Montserrat-Regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: 'Montserrat-Medium';
  src: url("../fonts/montserrat/Montserrat-Medium.woff2") format("woff2"), url("../fonts/montserrat/Montserrat-Medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: 'Sacramento-Regular';
  src: url("../fonts/sacramento/Sacramento-Regular.woff2") format("woff2"), url("../fonts/sacramento/Sacramento-Regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap; }

/* BUTTONS */
.btn {
  font-family: "Montserrat-Medium";
  padding: 12px 20px; }
  .btn-primary {
    color: #152131;
    background-color: #ffbe0b;
    border-color: #ffbe0b;
    border-bottom: 2px solid #d79e00; }
    .btn-primary:hover, .btn-primary:focus, .btn-primary:active {
      color: #152131;
      background-color: #d79e00;
      border-color: #d79e00;
      border-bottom: 2px solid #d79e00; }
  .btn-danger {
    color: #ffffff;
    border-color: #ec0101;
    background-color: #ec0101;
    border-bottom: 2px solid #b90101; }
    .btn-danger:hover, .btn-danger:focus, .btn-danger:active {
      color: #ffffff;
      border-color: #b90101;
      background-color: #b90101;
      border-bottom: 2px solid #b90101; }
  .btn-outline-white {
    border-color: #ffffff;
    color: #ffffff; }
  @media (min-width: 768px) {
    .btn-large {
      font-size: 20px; } }

/* SECTION */
.section {
  padding: 64px 0; }

/* HERO LIST */
.hero-list-item:first-child {
  margin-right: 30px; }
  .hero-list-item:first-child:before {
    content: '';
    right: -15px;
    background-color: rgba(255, 255, 255, 0.5);
    width: 4px;
    height: 4px; }

/* VIDEO BOX */
.video-box {
  border: 4px solid rgba(255, 255, 255, 0.25);
  box-shadow: 0 1px 4px 0 black; }

/* CTA BOX */
.cta-box {
  margin: 60px 0; }
  @media (min-width: 576px) {
    .cta-box {
      margin: 100px 0; } }
  @media (min-width: 768px) {
    .cta-box {
      margin: 160px 0; } }
  @media (min-width: 992px) {
    .cta-box {
      margin: 220px 0; } }
  @media (min-width: 1200px) {
    .cta-box {
      margin: 280px 0; } }
  @media (min-width: 1400px) {
    .cta-box {
      margin: 360px 0; } }

/* LIST COUNTER */
.list-counter {
  counter-reset: counter; }
  .list-counter > li:before {
    counter-increment: counter;
    content: counter(counter) ". "; }

/* PRODUCT */
.products {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  grid-gap: 30px; }
  @media (min-width: 576px) {
    .products {
      grid-template-columns: repeat(2, minmax(0, 1fr)); } }
  @media (min-width: 768px) {
    .products {
      align-items: center;
      grid-template-columns: repeat(3, minmax(0, 1fr));
      grid-gap: 40px; } }
  @media (min-width: 1200px) {
    .products {
      width: calc(100% + 100px);
      margin-left: -50px; } }

.product {
  padding: 16px;
  box-shadow: 0 1px 4px 0 #56191d;
  border-radius: 4px;
  transition: all ease-in-out .2s;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 15px;
  flex-shrink: 0; }
  .product:hover {
    transform: scale(1.02); }
  @media (min-width: 768px) {
    .product {
      padding: 40px 32px 10px 32px;
      grid-template-columns: repeat(1, 1fr); } }
  @media (min-width: 768px) {
    .product:first-child {
      order: 1; } }
  @media (min-width: 768px) {
    .product:nth-child(2) {
      order: 3; } }
  .product--recommended {
    padding-top: 64px; }
    @media (min-width: 768px) {
      .product--recommended {
        order: 2;
        padding-top: 80px; } }
    @media (min-width: 576px) and (max-width: 767.98px) {
      .product--recommended {
        grid-column: span 2; } }
  @media (min-width: 768px) {
    .product .recommended-gap {
      margin-bottom: 16px !important; } }
  @media (min-width: 768px) {
    .product .btn-cta {
      font-size: 20px; } }

.product__img {
  max-width: 100%;
  max-height: 133px; }

.product__price {
  font-size: 32px; }
  @media (min-width: 992px) {
    .product__price {
      font-size: 40px; } }
  @media (min-width: 1200px) {
    .product__price {
      font-size: 54px; } }

.product__price-subtitle {
  font-size: 12px; }
  @media (min-width: 992px) {
    .product__price-subtitle {
      font-size: 14px; } }
  @media (min-width: 1200px) {
    .product__price-subtitle {
      font-size: 16px; } }

.product__label {
  width: 64px;
  height: 64px;
  bottom: 0;
  border: 3px solid gold;
  pointer-events: none;
  transform: rotate(-20deg); }
  @media (min-width: 768px) {
    .product__label {
      bottom: 24px; } }

.product__label-recommended {
  width: calc(100% + 20px);
  height: 40px;
  position: absolute;
  top: 8px;
  margin-left: -10px;
  margin-right: -10px; }
  .product__label-recommended:before, .product__label-recommended:after {
    width: 0;
    height: 0;
    position: absolute;
    bottom: -10px;
    content: "";
    border-top: 10px solid #ccac00; }
  .product__label-recommended:before {
    left: 0;
    border-left: 10px solid transparent; }
  .product__label-recommended:after {
    right: 0;
    border-right: 10px solid transparent; }

.product__label-recommended-stars {
  line-height: 0; }

.product__label-recommended-stars__items {
  width: 12px;
  height: 12px;
  color: inherit;
  outline: none;
  line-height: 0; }
  .product__label-recommended-stars__items .icon {
    fill: currentColor;
    flex-grow: 0;
    flex-shrink: 0;
    width: inherit;
    height: inherit; }

/* ICON BOX */
.icon-box {
  width: 48px;
  height: 48px;
  color: inherit;
  outline: none;
  line-height: 0;
  margin-bottom: 24px;
  display: inline-block; }
  @media (min-width: 768px) {
    .icon-box {
      width: 64px;
      height: 64px; } }
  .icon-box .icon {
    fill: currentColor;
    flex-grow: 0;
    flex-shrink: 0;
    width: inherit;
    height: inherit; }

/* feedback */
.feedbacks:not(:last-child) {
  margin-bottom: 25px; }

.feedback {
  position: relative;
  padding: 32px 0 24px 0; }
  .feedback + .feedback {
    margin-top: 48px; }

.feedback__quote {
  width: 20px;
  height: 20px;
  color: inherit;
  outline: none;
  line-height: 0;
  position: absolute; }
  .feedback__quote--top {
    top: 0;
    left: 0; }
  .feedback__quote--bottom {
    right: 0;
    bottom: 75px; }
  .feedback__quote .icon {
    fill: currentColor;
    flex-grow: 0;
    flex-shrink: 0;
    width: inherit;
    height: inherit; }

.feedback__rating {
  margin-bottom: 12px; }

.feedback__rating-icons {
  width: 104px;
  height: auto;
  color: inherit;
  line-height: 0; }
  .feedback__rating-icons .icon {
    fill: currentColor;
    flex-grow: 0;
    flex-shrink: 0;
    width: inherit;
    height: inherit; }

.feedback__body {
  font-style: italic; }

/* FAQ */
.faq + .faq {
  margin-top: 32px; }

.faq__icon {
  width: 24px;
  height: 24px;
  color: inherit;
  line-height: 0;
  margin-top: 3px; }

/* MODAL */
.modal-backdrop.show {
  opacity: .9; }

/* MODAL EXIT */
@media (min-width: 768px) {
  .modal-exit .modal-dialog {
    max-width: 560px; } }

@media (min-width: 768px) {
  .modal-exit .modal-header {
    padding-top: 32px;
    padding-bottom: 0; } }

@media (min-width: 768px) {
  .modal-exit .modal-header,
  .modal-exit .modal-body,
  .modal-exit .modal-footer {
    padding-left: 48px;
    padding-right: 48px; } }

.modal-anchor-img {
  border: 4px solid red; }
